export default "import React from 'react';\r\nimport clsx from 'clsx';\r\nimport {makeStyles} from '@material-ui/core/styles';\r\nimport MenuItem from '@material-ui/core/MenuItem';\r\nimport TextField from '@material-ui/core/TextField';\r\n\r\nconst currencies = [\r\n  {\r\n    value: 'USD',\r\n    label: '$',\r\n  },\r\n  {\r\n    value: 'EUR',\r\n    label: '€',\r\n  },\r\n  {\r\n    value: 'BTC',\r\n    label: '฿',\r\n  },\r\n  {\r\n    value: 'JPY',\r\n    label: '¥',\r\n  },\r\n];\r\n\r\nconst useStyles = makeStyles((theme) => ({\r\n  container: {\r\n    display: 'flex',\r\n    flexWrap: 'wrap',\r\n  },\r\n  textField: {\r\n    marginLeft: theme.spacing(1),\r\n    marginRight: theme.spacing(1),\r\n  },\r\n  dense: {\r\n    marginTop: theme.spacing(2),\r\n  },\r\n  menu: {\r\n    width: 200,\r\n  },\r\n}));\r\n\r\nexport default function OutlinedTextFields() {\r\n  const classes = useStyles();\r\n  const [values, setValues] = React.useState({\r\n    name: 'Cat in the Hat',\r\n    age: '',\r\n    multiline: 'Controlled',\r\n    currency: 'EUR',\r\n  });\r\n\r\n  const handleChange = (name) => (event) => {\r\n    setValues({...values, [name]: event.target.value});\r\n  };\r\n\r\n  return (\r\n    <form className={classes.container} noValidate autoComplete='off'>\r\n      <TextField\r\n        id='outlined-name'\r\n        label='Name'\r\n        className={classes.textField}\r\n        value={values.name}\r\n        onChange={handleChange('name')}\r\n        margin='normal'\r\n        variant='outlined'\r\n      />\r\n      <TextField\r\n        id='outlined-uncontrolled'\r\n        label='Uncontrolled'\r\n        defaultValue='foo'\r\n        className={classes.textField}\r\n        margin='normal'\r\n        variant='outlined'\r\n      />\r\n      <TextField\r\n        required\r\n        id='outlined-required'\r\n        label='Required'\r\n        defaultValue='Hello World'\r\n        className={classes.textField}\r\n        margin='normal'\r\n        variant='outlined'\r\n      />\r\n      <TextField\r\n        error\r\n        id='outlined-error'\r\n        label='Error'\r\n        defaultValue='Hello World'\r\n        className={classes.textField}\r\n        margin='normal'\r\n        variant='outlined'\r\n      />\r\n      <TextField\r\n        disabled\r\n        id='outlined-disabled'\r\n        label='Disabled'\r\n        defaultValue='Hello World'\r\n        className={classes.textField}\r\n        margin='normal'\r\n        variant='outlined'\r\n      />\r\n      <TextField\r\n        id='outlined-email-input'\r\n        label='Email'\r\n        className={classes.textField}\r\n        type='email'\r\n        name='email'\r\n        autoComplete='email'\r\n        margin='normal'\r\n        variant='outlined'\r\n      />\r\n      <TextField\r\n        id='outlined-password-input'\r\n        label='Password'\r\n        className={classes.textField}\r\n        type='password'\r\n        autoComplete='current-password'\r\n        margin='normal'\r\n        variant='outlined'\r\n      />\r\n      <TextField\r\n        id='outlined-read-only-input'\r\n        label='Read Only'\r\n        defaultValue='Hello World'\r\n        className={classes.textField}\r\n        margin='normal'\r\n        InputProps={{\r\n          readOnly: true,\r\n        }}\r\n        variant='outlined'\r\n      />\r\n      <TextField\r\n        id='outlined-dense'\r\n        label='Dense'\r\n        className={clsx(classes.textField, classes.dense)}\r\n        margin='dense'\r\n        variant='outlined'\r\n      />\r\n      <TextField\r\n        id='outlined-dense-multiline'\r\n        label='Dense multiline'\r\n        className={clsx(classes.textField, classes.dense)}\r\n        margin='dense'\r\n        variant='outlined'\r\n        multiline\r\n        rowsMax='4'\r\n      />\r\n      <TextField\r\n        id='outlined-multiline-flexible'\r\n        label='Multiline'\r\n        multiline\r\n        rowsMax='4'\r\n        value={values.multiline}\r\n        onChange={handleChange('multiline')}\r\n        className={classes.textField}\r\n        margin='normal'\r\n        helperText='hello'\r\n        variant='outlined'\r\n      />\r\n      <TextField\r\n        id='outlined-multiline-static'\r\n        label='Multiline'\r\n        multiline\r\n        rows='4'\r\n        defaultValue='Default Value'\r\n        className={classes.textField}\r\n        margin='normal'\r\n        variant='outlined'\r\n      />\r\n      <TextField\r\n        id='outlined-helperText'\r\n        label='Helper text'\r\n        defaultValue='Default Value'\r\n        className={classes.textField}\r\n        helperText='Some important text'\r\n        margin='normal'\r\n        variant='outlined'\r\n      />\r\n      <TextField\r\n        id='outlined-with-placeholder'\r\n        label='With placeholder'\r\n        placeholder='Placeholder'\r\n        className={classes.textField}\r\n        margin='normal'\r\n        variant='outlined'\r\n      />\r\n      <TextField\r\n        id='outlined-textarea'\r\n        label='Multiline Placeholder'\r\n        placeholder='Placeholder'\r\n        multiline\r\n        className={classes.textField}\r\n        margin='normal'\r\n        variant='outlined'\r\n      />\r\n      <TextField\r\n        id='outlined-number'\r\n        label='Number'\r\n        value={values.age}\r\n        onChange={handleChange('age')}\r\n        type='number'\r\n        className={classes.textField}\r\n        InputLabelProps={{\r\n          shrink: true,\r\n        }}\r\n        margin='normal'\r\n        variant='outlined'\r\n      />\r\n      <TextField\r\n        id='outlined-search'\r\n        label='Search field'\r\n        type='search'\r\n        className={classes.textField}\r\n        margin='normal'\r\n        variant='outlined'\r\n      />\r\n      <TextField\r\n        id='outlined-select-currency'\r\n        select\r\n        label='Select'\r\n        className={classes.textField}\r\n        value={values.currency}\r\n        onChange={handleChange('currency')}\r\n        SelectProps={{\r\n          MenuProps: {\r\n            className: classes.menu,\r\n          },\r\n        }}\r\n        helperText='Please select your currency'\r\n        margin='normal'\r\n        variant='outlined'>\r\n        {currencies.map((option) => (\r\n          <MenuItem key={option.value} value={option.value}>\r\n            {option.label}\r\n          </MenuItem>\r\n        ))}\r\n      </TextField>\r\n      <TextField\r\n        id='outlined-select-currency-native'\r\n        select\r\n        label='Native select'\r\n        className={classes.textField}\r\n        value={values.currency}\r\n        onChange={handleChange('currency')}\r\n        SelectProps={{\r\n          native: true,\r\n          MenuProps: {\r\n            className: classes.menu,\r\n          },\r\n        }}\r\n        helperText='Please select your currency'\r\n        margin='normal'\r\n        variant='outlined'>\r\n        {currencies.map((option) => (\r\n          <option key={option.value} value={option.value}>\r\n            {option.label}\r\n          </option>\r\n        ))}\r\n      </TextField>\r\n      <TextField\r\n        id='outlined-full-width'\r\n        label='Label'\r\n        style={{margin: 8}}\r\n        placeholder='Placeholder'\r\n        helperText='Full width!'\r\n        fullWidth\r\n        margin='normal'\r\n        variant='outlined'\r\n        InputLabelProps={{\r\n          shrink: true,\r\n        }}\r\n      />\r\n      <TextField\r\n        id='outlined-bare'\r\n        className={classes.textField}\r\n        defaultValue='Bare'\r\n        margin='normal'\r\n        variant='outlined'\r\n        inputProps={{'aria-label': 'bare'}}\r\n      />\r\n    </form>\r\n  );\r\n}\r\n";